import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/HomeView.vue";
import Skills from "../views/SkillsView.vue";
import Projects from "../views/ProjectsView.vue";
import Certification from "../views/CertificationView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/skills",
    name: "Skills",
    component: Skills
  },
  {
    path: "/projects",
    name: "Projects",
    component: Projects
  },
  {
    path: "/certification",
    name: "Certification",
    component: Certification
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
