import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    info: {
      Projects: [],
      Certifications: []
    },
    loading: true,
    errored: false
  },
  actions: {
    async fetchInfo({ commit }) {
      try {
        const response = await axios.get("https://vue-portfolio-b49b1.firebaseio.com/info.json");
        commit("SET_INFO", response.data);
      } catch (error) {
        console.log(error);
        commit("SET_ERROR");
      } finally {
        commit("SET_LOADING", false);
      }
    }
  },
  mutations: {
    SET_INFO(state, res) {
      state.info = res;
    },
    SET_ERROR(state) {
      state.errored = true;
    },
    SET_LOADING(state, value) {
      state.loading = value;
    }
  }
});